.wrapper {
    margin: 5% auto
}

/*filters*/
.sport-filters {
    margin: 5% 0;
}

.sport-filters .sport-checkbox {
    padding: 0.3em;
}

.sport-filters .sport-checkbox .selector {
    border-radius: 0.5em;
    text-align: center;
    padding: 0.3em;
    font-weight: bold;
}

.sport-filters .sport-checkbox .selected {
    background-color: #4a65cc;
    color: #d7e3f4
}

.sport-filters .sport-checkbox .unselected {
    background-color: #96a8d7;
}

/*table*/
.events-table {
    margin-top: 5%;
    margin-bottom: 5%;
}

.events-table div {
    padding: 0.2em;
}

.events-table .table-header {
    position: sticky;
    top: 0;
    background-color: #96a8d7;
    border-radius: 0.5em;
}

.events-table .table-header .event-name {
    padding: 0.5em 0.5em 0.5em 2em;
}

.events-table .table-header .event-name h6 {
    font-size: 1.3em;
    font-weight: bold;
}

.events-table .table-header .counter {
    border: 0;
    background-color: #4a65cc;
    border-radius: 1.5em;
    color: #d7e3f4;
    vertical-align: middle;
    font-size: 1.1em;
    font-weight: bold;
}

.events-table .line {
    margin: 0;
    padding: 0;
}

.events-table .line .level-1 h6 {
    font-weight: 650;
    padding-left: 1em;
}

.events-table .line .level-2 {
    padding-left: 2em;
}

.events-table .line .level-3 {
    padding-left: 3em;
}

.events-table .line .level-4 {
    padding-left: 4em;
}

.events-table .line .counters {
    display: flex;
}

.events-table .line .counters .counter {
    min-width: 9%;
    margin: 0 auto;
    text-align: center;
    border-left: 1px solid #96a8d7;
}

.events-table .line .counters .counter p {
    margin: 0;
    padding: 0;
}


.events-table .table-body .foldable .opened .level-1 {
    font-weight: 650;
    padding-left: 1em;
}

.events-table .table-body .foldable .opened .level-2 {
    padding-left: 2em;
}

.events-table .table-body .foldable .opened .level-3 {
    padding-left: 3em;
}

.events-table .table-body .foldable .opened .level-4 {
    padding-left: 4em;
}